body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#main-nav {
    background-color: #007F09;
}

a.brand-logo {
    margin-left: 20px;
}

.home-card {
    background-color: transparent;
    border-radius: 10px;
}

footer {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
}

footer.page-footer {
    background-color: #007F09;
}

.btn {
    font-weight: bolder;
    font-size: 15px;
    background-color: #007F09;
    width: 100%;
    border-radius: 10px;
}

.home-btn {
    height: 200px;
    margin-bottom: 20px;
}

.btn:hover {
    background-color: #00BF0D;
    width: 100%;
}

#home-logo {
    width: 100%;
    -webkit-animation: fadein 3s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 3s;
}

#home-list, #home-pantry, #home-waste, #home-recipes, #home-logout, #home-account, #home-login, #home-signup, #signup-form {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

#home-list {
    background-image: url("/img/baseline-list-24px.svg");
}

#home-pantry {
    background-image: url("/img/baseline-kitchen-24px.svg");
}

#home-waste {
    background-image: url("/img/baseline-pie_chart-24px.svg");
}

#home-recipes {
    background-image: url("/img/baseline-find_in_page-24px.svg");
}

#home-logout {
    background-image: url("/img/baseline-exit_to_app-24px.svg");
}

#home-account, #home-login {
    background-image: url("/img/baseline-account_circle-24px.svg");

}

#home-signup {
    background-image: url("/img/baseline-create-24px.svg");
}

#signup-form {
    background-image: url("/img/baseline-account_circle-24px-green.svg");
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */

/* Opera < 12.1 */

body {
    background-color: #00E510;
}
